.papp{
    padding-top: 50px;
    position: relative;
    text-decoration: none;
}

@media screen and (max-width: 760px) {
    .papp{
         padding-top: 15px;
         padding-bottom: 15px;
    }    
}

.pappImg{ 
    width: 100%;
    height: 80%;
    -webkit-box-shadow: 5px 5px 10px rgba(0,0,0, 0.5);
            box-shadow: 5px 5px 10px rgba(0,0,0, 0.5);
}

@media screen and (max-width: 760px) {
    .pappImg{
         height: 800px;
         -webkit-box-shadow: 5px 5px 10px rgba(0,0,0, 0.5);
                 box-shadow: 5px 5px 10px rgba(0,0,0, 0.5);
    }    
}

.text-block-rubrik1{
    position: absolute;
    left: 45%;
    top: 5%;
}

.text-block{
    position: absolute;
    top: 25%;
    margin-left: 40px;
    line-height: 2.8;
    font-size: 20px;
    width: 90%;
}

.uppgifter{
    list-style-type: none;
    padding-left: 0px;
    width: 50%;
}

@media screen and (max-width: 760px) {
    .uppgifter{
        width: 100%;
    }    
}


.inputNone{
    border: none;
    background-color: transparent;
    border-radius: 1px;
    border-bottom-style: dotted;
    padding-top: 33px;
    outline: none;
}

@media screen and (max-width: 760px) {
    .inputNone{
        width: 150px;
    }    
}


.text-block-rubrik2{
    position: absolute;
    left: 460px;
    top: 0%;
    font-size: 20px;
}

@media screen and (max-width: 760px) {
    .text-block-rubrik2{
     visibility: hidden;
         
    }    
}

.text-block-rubrik3{
    position: absolute;
    right: 50px;
    top: 0%;
    font-size: 20px;
}

@media screen and (max-width: 760px) {
    .text-block-rubrik3{
     visibility: hidden;
         
    }    
}

.check-block{
    position: absolute;
    left: 460px;
    bottom: 30%;
    line-height: 2.8;
    font-size: 20px;
}

@media screen and (max-width: 760px) {
    .check-block{
    left: 0px;
    top: 270px;    
         
    }    
}

.checkText{
    display: inline;
    margin-left: 10px;
    font-size: 18px;
}

.select-block{
    position: absolute;
    right: 27px;
    top: 68px;
    font-size: 16px;
    font-family: 'Cutive Mono'; 
}

@media screen and (max-width: 760px) {
    .select-block{
    left: 0px;
    top: 400px;     
    }    
}


.select{
    border: none;
    background-color: transparent;
    outline: none; 
    width: 176px;
}

.butBoka{
    position: absolute;
    right: 30px;
    top: 240px;
}

@media screen and (max-width: 760px) {
    .butBoka{
    left: 0px;    
    top: 470px;     
    }    
}

.bekraftelse{
    width: 230px;
    font-size: 13px;
    font-family: 'Cutive Mono'; 
    -webkit-box-shadow: 5px 5px 10px rgba(0,0,0, 0.5); 
            box-shadow: 5px 5px 10px rgba(0,0,0, 0.5);
    background-color: transparent;
    outline: none;
}














