header{
    font-weight: bold;
    text-align:center;
    display:block;
    width: 100%;
    color: #FFFFFF;
    background-color:#5C5C5D;
    font-family: 'Cutive Mono';
    font-size:  19px;
    border: #5C5C5D solid 1px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    overflow: hidden;
    position: fixed;
    z-index: 1;  
}

.logo{
    padding-top: 20px;
    /*padding-bottom: 30px;*/
}

.nav-menu .nav-item{
    list-style-type: none;
    display: inline-block;
    margin-left: 20px;
    margin-right: 20px;
}

.nav-menu{
    padding-top: 40px;
    margin-bottom: 0px;
     /*border: 1px solid red;*/
}

a{
    font-family: 'Cutive Mono';
    font-size:  19px;
    line-height: normal;
    color: #FFFFFF;
    cursor: pointer;
    margin-right: 5px;
}

a:hover{
    color: #BFD3C3;
    background: #5C5C5D !important;
    text-decoration: underline !important;
}



/**** responsiv header ****/
@media screen and (min-width: 760px) {
    .header {
        padding-left: 260px; 
        /*height: 55px;*/
    }    
}

/**** hamburger-menyn ****/
.icon-bar{
    background-color: #FFFFFF;
    width: 28px;
}


@media screen and (max-width: 760px) {
    .nav-menu{
          /*border: red solid 1px;*/
          text-align: left;
          padding-left: 0px; 
    }    
}

@media screen and (max-width: 760px) {
    .nav-item{
          width:100%;  
    }    
}



